import React, { Component } from "react";
import Title from "./head/title";//
import Message from "./head/message";//
import Nav from "./head/nav";//
import "./head.css";

class Head extends Component{
	render(){
		return(
			<div className="head">
				<Title />
				<Message />
				<Nav />
			</div>
		);
	}
}
export default Head;




