import React,{Component} from "react";

class Message extends Component{
	render(){
		return(
			<div className="message">
				<div className="leftM">
				</div>
				<div className="rightM">
					<div className="timer">2017.04.23-2017.05.12</div>
					<div className="detail">...</div>
					<div className="btn">
						<span></span>
                    	<span></span>
					</div>
				</div>
			</div>     
		);
	}
}
export default Message;


